.particleWindow {
    background-color: #438cff !important;
    height: 100%;
}

.false{
    background-color: #438cff !important;
    -webkit-animation-name: red2blue;
            animation-name: red2blue;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.true{
    background-color: #BE2625 !important;
    -webkit-animation-name: blue2red;
            animation-name: blue2red;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes red2blue {
    from{background-color: #BE2625;}
    to{background-color: #438cff;}
}

@keyframes red2blue {
    from{background-color: #BE2625;}
    to{background-color: #438cff;}
}

@-webkit-keyframes blue2red {
    from{background-color: #438cff;}
    to{background-color: #BE2625;}
}

@keyframes blue2red {
    from{background-color: #438cff;}
    to{background-color: #BE2625;}
}


